<template>
    <v-container >
            <v-dialog color="dark" dark max-width="600px" v-model="dialog" persistent>

            <v-card style="border:1px solid #D3D3D3">
                <v-card-title>
                   
                    <v-icon color="dark" left>
                        mdi-tag-plus
                    </v-icon>
                    <span class="hidden-xs-only">New Coupon [ <span style="color:green">{{client.email}} </span>]</span>
                    <span class="hidden-sm-and-up"> <div> New Coupon </div> <div> <span style="color:green"> {{client.email}} </span> </div> </span>
                    </v-card-title>
                    <v-card-text>
                        <template>
                            <v-form v-model="valid" ref="form" lazy-validation>
                                <v-container style="padding:50px">
                                    <v-row>
                                        <v-col
                                        cols="12"
                                        md="12"
                                        >
                                            <v-text-field
                                                v-model="percent"
                                                :rules="percentRules"
                                                :counter="2"
                                                label="Coupon Percent"
                                                required
                                                prefix="%"
                                            ></v-text-field>

                                        </v-col>
                                    </v-row>
                                
                                <v-row>
                                    <v-col
                                        cols="12"
                                        md="6"
                                    />
                                    <v-col
                                        cols="12"
                                        md="3"
                                        sm="4"
                                    >
                                    
                                        <v-btn
                                            color="green darken-1"
                                            text
                                            @click="addC()"
                                        >
                                        <v-icon color="dark" left>
                                            mdi-check-circle-outline
                                        </v-icon>
                                            Confirm
                                        </v-btn>
                                     </v-col>

                                    <v-col
                                        cols="12"
                                        md="3"
                                        sm="4"
                                    >
                                        <v-btn
                                            color="red darken-1"
                                            text
                                            @click="dialog = false;  $refs.form.reset();"
                                        >
                                        <v-icon color="dark" left>
                                            mdi-close-circle-outline
                                        </v-icon>
                                           Close
                                        </v-btn>
                                    </v-col>
                                    
                                </v-row>

                                </v-container>
                            </v-form>
                        </template>
                    </v-card-text>

                </v-card>
            </v-dialog>

            <!--procced dialog -->
            <v-dialog
            v-model="dialogLoading"
            hide-overlay
            persistent
            width="450"
            min-height="350"
            >

            <Loader/>
               
            </v-dialog>
    </v-container>
</template>

<script>
import axios from "../../../axios/index"
import Loader from "../../Loaders/Loader"

export default {

    data(){

        return{
            dialog:false,
            dialogLoading:false,
            valid:false,
            client:{},
            // rules
            percentRules: [
                v => !!v || 'Percent is required',
                v => v!=null || 'Percent is required',
                v => v!="" || 'Percent is required',
                v => v!=null && v.length <= 2 || 'Percent must be less than 2 characters or equal.',
                v => !isNaN(v) || 'Percent must be number.',
                v => v!=null && !String(v).includes("-") || 'Percent must be positive number.',
                v => v!=null && !String(v).includes("+") || 'Percent invalid format.',
                v => v!=0 || 'Percent can not be 0.',

            ],
            // v-models
            percent:"0",
        };
    },

    components:{
        Loader,
    },

    methods:{
        async addC(){
            if(this.$refs.form.validate()){
                event.preventDefault()
                this.dialogLoading = true
                await axios.post('/coupon',{
                    percent:this.percent,
                    email:this.client.email
                }).then( async()=>{
                    await this.$store.dispatch("setCoupons", this.client.email)
                    this.$refs.form.reset()
                    this.dialogLoading = false
                    this.dialog = false
                }).catch(()=>{
                    this.dialogLoading = false
                })   
               
            }
        }
        
    }
}
</script>

<style scoped>

</style>